import useStore from "@/context/store";
import PriceFormatter from "../format-price/PriceFormatter";
import { useEffect, useState } from "react";
import { PageLoader } from "../loader";
import { Check, CircleAlert, X } from "lucide-react";
import { HeartIcon } from "@/assets/icons";

interface Props {
  onNextStep: () => void;
}

export const MyCart = ({ onNextStep }: Props) => {
  const [city, setCity] = useState<string>("");
  const [isClient, setIsClient] = useState(false);
  const { getTotalPrice, cart } = useStore();
  console.log("cart", cart);

  useEffect(() => setIsClient(true), []);
  if (!isClient) {
    return <PageLoader />;
  }
  return (
    <div className="grid grid-cols-4 gap-[23px]">
      {/* left */}
      <div className="col-span-3">
        <div className="space-y-6">
          <div className="bg-white border shadow-sectionShadow py-[23px] px-[15px]">
            <div className="flex items-center gap-2">
              <h5 className="text-textColor text-base">
                Выберите город доставки
              </h5>
              {city === "Ташкент" ? (
                <div className="w-5 h-5 bg-green-600 rounded-full flex items-center justify-center">
                  <Check className="w-3 h-3 text-white" />
                </div>
              ) : (
                <div className="flex items-center gap-2">
                  <span className="">
                    <CircleAlert className="w-4 h-4 text-orangeSun" />
                  </span>
                  <p className="text-xs">
                    Уточните ваш город, это необходимо для корректных расчётов
                    способов доставки или самовывоза.
                  </p>
                </div>
              )}
            </div>
            <div className="mt-4 mb-2">
              <input
                type="text"
                className="inputs py-2.5"
                placeholder="Начните вводить название города"
                value={city || ""}
                onChange={(e) => setCity(e.target.value)}
              />
            </div>
            <button
              className={`px-[15px] h-[30px] flex items-center justify-center rounded-full  text-xs ${!city ? "bg-superSilver text-textColor " : "bg-cerulean text-white"}`}
              onClick={() => setCity("Ташкент")}
            >
              Ташкент
              {city && (
                <span
                  className="ml-1"
                  onClick={(e) => {
                    e.stopPropagation();
                    setCity("");
                  }}
                >
                  <X className="w-4 h-4" />
                </span>
              )}
            </button>
          </div>
          <div className="bg-white border shadow-sectionShadow py-[23px] px-[15px] text-textColor">
            <div className="flex items-center justify-between">
              <div className="flex items-center gap-2">
                <input
                  id="all-check"
                  type="checkbox"
                  className="bg-green-600 cursor-pointer w-[18px] h-[18px] checked:bg-green-600"
                />
                <label htmlFor="all-check" className="cursor-pointer">
                  выбрать все
                </label>
              </div>
              <div>
                <span className="text-sm font-normal text-textColor cursor-pointer">
                  Очистить корзину
                </span>
              </div>
            </div>
          </div>
          {/* cart map */}
          {cart?.map((product) => (
            <div
              key={product.id}
              className="p-[15px] bg-white border shadow-sectionShadow text-textColor"
            >
              {/* header */}
              <div className="flex justify-between  ">
                <input type="checkbox" />
                <div className="flex h-[26px] items-center">
                  <button className="flex items-center gap-2 group ">
                    <span className="text-xs group-hover:opacity-70 duration-200 ease-in-out">
                      перенести в избранное
                    </span>
                    <span>
                      <HeartIcon className="w-[18px] h-[18px] group-hover:opacity-70 duration-200 ease-in-out" />
                    </span>
                  </button>
                  <div className="w-[2px] h-full  bg-superSilver mx-[15px]"></div>
                  <button className="flex items-center gap-2 group ">
                    <span className="text-xs group-hover:opacity-70 duration-200 ease-in-out">
                    удалить из корзины
                    </span>
                    <span>
                      <Trash2 className="w-[18px] h-[18px] group-hover:opacity-70 duration-200 ease-in-out" />
                    </span>
                  </button>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
      <div className="col-span-1">
        <div className="bg-white border shadow-sectionShadow p-[23px]">
          <PriceFormatter amount={getTotalPrice()} />
        </div>
      </div>
      {/* right */}
      <button onClick={onNextStep}>gg</button>
    </div>
  );
};

export default MyCart;
